import * as Yup from "yup"
import translate from "../../config/yup"

export const initialValues = {
	codigo: 0,
	nome: "",
	email_login: "",
	id_denominacao: 0,
	cliente_web: "1",
	status: "1",
}

Yup.setLocale(translate)
export const validationSchema = () => {
	return Yup.object().shape({
		codigo: Yup.string().min(1).max(10).required(),
		email_login: Yup.string().email().max(100).required(),

		nome: Yup.string().min(3).max(60).required(),
	})
}

export const camposForm = (props) => {
	return [
		[
			{
				config: { xs: 6, sm: 6, xl: 6 },
				type: "column",
				fields: [
					{ family: "component", type: "label", label: "Identificação" },
					{
						family: "input",
						type: "text",
						label: "Nome",
						name: "nome",
						config: {
							disabled: props.loading,
							max: 50,
						},
					},
					{
						family: "input",
						type: "select",
						label: "Denominação",
						name: "id_denominacao",
						config: {
							disabled: props.loading,
						},
						options: props.denominacoes,
					},
					{
						family: "input",
						type: "number",
						label: "Código",
						name: "codigo",
						config: {
							disabled: props.loading,
							max: 10,
						},
					},
					{ family: "component", type: "label", label: "Informações de login" },
					{
						family: "input",
						type: "email",
						label: "e-mail",
						name: "email_login",
						config: {
							disabled: props.loading,
							max: 100,
						},
					},
				],
			},
			{
				config: { xs: 6, sm: 6, xl: 6 },
				type: "column",
				fields: [
					{
						family: "input",
						type: "checkbox",
						label: "Cliente Web",
						name: "cliente_web",
						config: {
							disabled: props.loading,
						},
					},
					{
						family: "input",
						type: "checkbox",
						label: "Ativo",
						name: "status",
						config: {
							disabled: props.loading,
						},
					},
				],
			},
		],
	]
}
