import React, { useState, useEffect } from "react"
import { Route, useHistory } from "react-router-dom"

import { formatFormData } from "../../forms/functions/form"
import * as clienteAPI from "../../services/functions/cliente"
import * as denominacaoAPI from "../../services/functions/denominacao"
import idToName from "../../functions/idToName"
import dataToSelect from "../../functions/dataToSelect"
import ErrorBoundary from "../../components/ErrorBoundary"
import Datatable from "../../components/Datatable"
import ModalForm from "../../components/ModalForm"
import ConfirmModal from "../../components/UI/ConfirmModal"
import {
  initialValues,
  validationSchema,
  camposForm,
} from "../../forms/forms/cliente"
import {
  initialValues as initialValuesEdit,
  validationSchema as validationSchemaEdit,
  camposForm as camposFormEdit,
} from "../../forms/forms/clienteEdit"
import {
  Edit,
  AddCircle,
  Delete,
  Sync,
  AddBox,
  IndeterminateCheckBox,
} from "@material-ui/icons"
import estilos from "./estilos.module.css"

const Clientes = (props) => {
  const { appState } = props

  const [clientes, setClientes] = useState([])
  const [clientesA, setClientesA] = useState([])
  const [cliente, setCliente] = useState(null)
  const [denominacoes, setDenominacoes] = useState([])
  const [linhaSelecionada, setLinhaSelecionada] = useState(null)

  const history = useHistory()
  const location = window.location.pathname.split("/")

  const colunasTabela = [
    {
      id: "nome",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Nome",
    },
    {
      id: "codigo",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Código",
    },
    {
      id: "id_denominacao",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Denominação",
    },
    {
      id: "email_login",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "e-mail",
    },
    {
      id: "status",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Ativo",
    },
    {
      id: "cliente_web",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Cliente Web",
    },
  ]

  const opcoesTabela = [
    {
      label: "Sincronizar Dados",
      click: () => fetchClientesHandler(),
      icon: <Sync />,
    },
    {
      label: "Novo",
      click: () => {
        history.push("/clientes/novo")
      },
      icon: <AddCircle />,
    },
  ]

  const opcoesTabelaSelecionada = [
    {
      label: "Editar",
      click: () => {
        fetchClienteHandler()
        history.push("/clientes/editar")
      },
      icon: <Edit />,
    },
    {
      label: "Excluir",
      click: () => {
        history.push("/clientes/excluir")
      },
      icon: <Delete />,
    },
    {
      label: "Criar Schema",
      click: () => {
        createSchemaClienteHandler()
      },
      icon: <AddBox />,
    },
    {
      label: "Excluir Schema",
      click: () => {
        history.push("/clientes/excluir-schema")
      },
      icon: <IndeterminateCheckBox />,
    },
  ]

  const idToNameClientes = () => {
    if (!clientes) {
      return
    }

    let clis = []

    if (denominacoes.length > 0) {
      clis = idToName([clientes, "id_denominacao"], [denominacoes, "nome"])
    }

    clis = idToName(
      [clis, "status"],
      [
        [
          { id: 1, nome: "Sim" },
          { id: 0, nome: "Não" },
        ],
        "nome",
      ]
    )

    clis = idToName(
      [clis, "cliente_web"],
      [
        [
          { id: 1, nome: "Sim" },
          { id: 0, nome: "Não" },
        ],
        "nome",
      ]
    )

    setClientesA(clis)
  }

  const successHandler = () => {
    setLinhaSelecionada(null)
    history.goBack()
    fetchClientesHandler()
    appState.setSuccess()
  }

  const fetchClientesHandler = async () => {
    const adms = await clienteAPI.fetchAll({ appState })

    setClientes(adms)
  }

  const fetchClienteHandler = async () => {
    const admin = await clienteAPI.fetchOne({
      id: linhaSelecionada,
      appState,
    })

    setCliente(admin)
  }

  const cadClienteHandler = async (values) => {
    const res = await clienteAPI.create({
      cliente: values,
      appState,
    })

    if (res) {
      successHandler()
    }
  }

  const editClienteHandler = async (values) => {
    const res = await clienteAPI.update({
      cliente: {
        ...values,
        id: linhaSelecionada,
      },
      appState,
    })

    if (res) {
      successHandler()
    }
  }

  const delClienteHandler = async () => {
    const res = await clienteAPI.del({
      cliente: {
        id: linhaSelecionada,
      },
      appState,
    })

    if (res) {
      successHandler()
    }
  }

  const createSchemaClienteHandler = async () => {
    const res = await clienteAPI.createSchemaCliente({
      cliente: {
        id: linhaSelecionada,
      },
      appState,
    })

    if (res) {
      appState.setSuccess()
    }
  }

  const delSchemaClienteHandler = async () => {
    const res = await clienteAPI.delSchemaCliente({
      cliente: {
        id: linhaSelecionada,
      },
      appState,
    })

    if (res) {
      appState.setSuccess()
    }

    history.goBack()
  }

  const fetchDenominacoesHandler = async () => {
    const res = await denominacaoAPI.fetchAll({ appState })

    setDenominacoes(res)
  }

  const onFormSubmit = async (values) => {
    let newValues = await formatFormData(values)

    if (location[2] === "novo") {
      cadClienteHandler(newValues)
    } else if (location[2] === "editar") {
      editClienteHandler(newValues)
    }
  }

  useEffect(() => {
    fetchClientesHandler()
    fetchDenominacoesHandler()
  }, [])

  useEffect(() => {
    idToNameClientes()
  }, [clientes, denominacoes])

  return (
    <ErrorBoundary>
      <div className={estilos.Container}>
        <Datatable
          caixaPesquisa={true}
          colunasKey={"id"}
          colunasTabela={colunasTabela}
          fetchedRows={clientesA}
          linhasPorPaginaSelecionada={15}
          linhaSelecionada={linhaSelecionada}
          opcoesTabela={opcoesTabela}
          loading={appState.loading}
          opcoesTabelaSelecionada={opcoesTabelaSelecionada}
          setLinhaSelecionada={setLinhaSelecionada}
          tituloTabela={"Clientes"}
        />
      </div>

      <Route path="/clientes/excluir">
        <ConfirmModal
          action={"delete"}
          history={history}
          location={location}
          okButtonClick={delClienteHandler}
        />
      </Route>

      <Route path="/clientes/excluir-schema">
        <ConfirmModal
          action={"delete"}
          history={history}
          location={location}
          okButtonClick={delSchemaClienteHandler}
        />
      </Route>

      <Route path="/clientes/novo">
        <ModalForm
          camposForm={camposForm({
            denominacoes: dataToSelect(denominacoes, "id", "nome"),
            loading: appState.loading,
          })}
          initialValues={initialValues}
          history={history}
          loading={appState.loading}
          location={location}
          onFormSubmit={onFormSubmit}
          tabLabel={[{ label: "Cliente" }]}
          validationSchema={validationSchema}
        />
      </Route>

      <Route path="/clientes/editar">
        {linhaSelecionada ? (
          <ModalForm
            camposForm={camposFormEdit({
              denominacoes: dataToSelect(denominacoes, "id", "nome"),
              loading: appState.loading,
            })}
            initialValues={cliente ? cliente : initialValuesEdit}
            history={history}
            loading={appState.loading}
            location={location}
            onFormSubmit={onFormSubmit}
            tabLabel={[{ label: "Cliente" }]}
            validationSchema={validationSchemaEdit}
          />
        ) : null}
      </Route>
    </ErrorBoundary>
  )
}

export default Clientes
